﻿import {h} from 'vue';

export default {
    name: 'DataGridRowGroup',
    props: {
        row: Object,
        left: Number,
        grid: Object
    },
    render(){
        return h(
            'div',
            {
                'class':'datagrid-group-title',
                'style':{left:this.left+'px'}
            },
            [
                this.grid.$slots['group'] ? this.grid.$slots['group']({
                    value: this.row.value,
                    rows: this.row.rows
                }) : this.row.value
            ]
        )
    }
}